@import "./footer-menu.scss";

.footer {
  padding-bottom: 40px;
  .footer-first-block {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 1000px) {
      flex-direction: column;
    }
    .burger-menu {
      display: none;
      @media (max-width: 860px) {
        display: block;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);
      }
    }
    .footer-menu {
      padding-top: 24px;
      flex: 0 0 77.1%;
      @media (max-width: 1000px) {
        flex: 0 0 auto;
        gap: 20px;
        padding-top: 10px;
      }
    }
    margin-bottom: 27px;
    @media (max-width: 860px) {
      margin-bottom: 8px;
    }
  }
  .hr {
    margin: 0px;
    height: 1px;
    background: rgba(0, 0, 0, 0.08);
    border: none;
  }
  .footer-second-block {
    padding-top: 27px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    &__develop {
      margin-left: -1px;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      color: $darkBlue;
      .green {
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 600;
        font-size: 12px;
        line-height: 15px;
        color: $darkGreen;
        letter-spacing: -0.007em;
      }
    }
    &__copyright {
      font-family: "Montserrat";
      padding-right: 95px;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      color: $darkBlue;
      text-align: center;
      @media (max-width: $tablet) {
        padding-right: 0;
      }
    }
    .social {
      padding-bottom: 5px;
      @media (max-width: $tablet) {
        padding-bottom: 0;
      }
    }
    @media (max-width: 600px) {
      padding-top: 15px;
      column-gap: 20px;
      row-gap: 10px;
    }
  }
  @media (max-width: 600px) {
    padding-bottom: 10px;
  }
}
