@import "../default.scss";

.footer__container {
  .footer-menu {
    display: flex;
    justify-content: space-between;

    @media (max-width: 860px) {
      display: none;
    }
    &__item {
      a {
        font-family: "Raleway";
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
        line-height: 18px;
        &.active{
          color: $darkGreen;
        }
      }
    }
  }
}
