.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.footer__container .footer-menu {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 860px) {
  .footer__container .footer-menu {
    display: none;
  }
}
.footer__container .footer-menu__item a {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
}
.footer__container .footer-menu__item a.active {
  color: #337d5a;
}

.footer {
  padding-bottom: 40px;
}
.footer .footer-first-block {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 27px;
}
@media (max-width: 1000px) {
  .footer .footer-first-block {
    flex-direction: column;
  }
}
.footer .footer-first-block .burger-menu {
  display: none;
}
@media (max-width: 860px) {
  .footer .footer-first-block .burger-menu {
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
  }
}
.footer .footer-first-block .footer-menu {
  padding-top: 24px;
  flex: 0 0 77.1%;
}
@media (max-width: 1000px) {
  .footer .footer-first-block .footer-menu {
    flex: 0 0 auto;
    gap: 20px;
    padding-top: 10px;
  }
}
@media (max-width: 860px) {
  .footer .footer-first-block {
    margin-bottom: 8px;
  }
}
.footer .hr {
  margin: 0px;
  height: 1px;
  background: rgba(0, 0, 0, 0.08);
  border: none;
}
.footer .footer-second-block {
  padding-top: 27px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.footer .footer-second-block__develop {
  margin-left: -1px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #1a2f3f;
}
.footer .footer-second-block__develop .green {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  color: #337d5a;
  letter-spacing: -0.007em;
}
.footer .footer-second-block__copyright {
  font-family: "Montserrat";
  padding-right: 95px;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #1a2f3f;
  text-align: center;
}
@media (max-width: 960px) {
  .footer .footer-second-block__copyright {
    padding-right: 0;
  }
}
.footer .footer-second-block .social {
  padding-bottom: 5px;
}
@media (max-width: 960px) {
  .footer .footer-second-block .social {
    padding-bottom: 0;
  }
}
@media (max-width: 600px) {
  .footer .footer-second-block {
    padding-top: 15px;
    column-gap: 20px;
    row-gap: 10px;
  }
}
@media (max-width: 600px) {
  .footer {
    padding-bottom: 10px;
  }
}